
.treemap-content {
  max-width: 1200px;
  margin: auto;
}
.control {
  position: static !important;
}
.controls {
  padding: 10px 0;
}
.controls .filters {
    float: right;
}
.controls .filters .filter {
      float: left;
      padding-left: 10px;
}
.controls .scales {
    float: left;
}
.controls .hierarchies {
    float: left;
}
.controls .hierarchies .hierarchy {
      float: left;
      padding-left: 10px;
}
.controls .measures {
    float: left;
}
.controls:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.treemap {
  min-height: 500px;
  overflow: hidden;
}
.node {
  display: block;
  border: 0;
  color: #fff;
  overflow: hidden;
  position: absolute;
  outline: 1px solid #fff;
  font-size: 0.8em;
  text-indent: -1000px;
  padding: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 5;
  font-weight: 300;
  text-shadow: -2px -2px 13px rgba(150, 150, 150, 0.9);
}
.node a, .node a:hover {
    color: #fff;
    text-decoration: none;
}
.node.big {
    text-indent: 2px;
}
.node.big .amount {
      color: #fff;
      display: block;
      padding-bottom: 0.2em;
      font-size: 1.3em;
      text-indent: 2px;
}
.node.big, .node.big:hover {
      color: #fff;
      text-decoration: none;
}
.node:hover {
    text-indent: 2px;
    text-decoration: none;
}
#download {
  text-align: left;
  margint-top: 5px;
}
#download a {
    color: black;
    border: 1px solid black;
    padding: 0 5px;
}
#download a:hover {
      color: white;
      background-color: black;
}
#table-toggle {
  height: 80px;
  margin: 0 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.table {
  width: 100%;
  margin-top: 1em;
}
.table td, .table th {
    font-size: 0.9em;
    text-align: left;
}
.table td.num, .table th.num {
    text-align: right;
}
.table .hide-small, .table .small {
    display: none;
}
.table .label a, .table .label a:hover {
    color: #fff;
    text-decoration: none;
    font-weight: 400;
}
@font-face {
  font-family: 'fa-minimal';
  src: url("../assets/fonts/fa-minimal.eot?xss5ew");
  src: url("../assets/fonts/fa-minimal.eot?xss5ew#iefix") format("embedded-opentype"), url("../assets/fonts/fa-minimal.ttf?xss5ew") format("truetype"), url("../assets/fonts/fa-minimal.woff?xss5ew") format("woff"), url("../assets/fonts/fa-minimal.svg?xss5ew#fa-minimal") format("svg");
  font-weight: normal;
  font-style: normal;
}
.fa {
  display: inline-block;
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'fa-minimal' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.fa-check:before {
  content: "\f00c";
}
.fa-info-circle:before {
  content: "\f05a";
}
.fa-square:before {
  content: "\f0c8";
}
.fa-plus-square:before {
  content: "\f0fe";
}
.fa-minus-square:before {
  content: "\f146";
}
.fa-level-up:before {
  content: "\f148";
}
.treemap-tooltip {
  position: absolute;
  text-align: center;
  padding: 10px;
  font: 12px sans-serif;
  background: #eee;
  border: 0px;
  border-radius: 8px;
  pointer-events: none;
  z-index: 10;
  display: block;
}
.table-div {
  max-height: 350px;
  overflow: scroll;
}
.sub {
  padding-left: 10px;
}
.top {
  font-weight: bold;
}
.breadcrumb.tc {
  font-size: 0.6em;
}



